@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/OpenSans-Light.woff2"), url("../fonts/OpenSans-Light.woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/OpenSans-Regular.woff2"), url("../fonts/OpenSans-Regular.woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/OpenSans-SemiBold.woff2"), url("../fonts/OpenSans-SemiBold.woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/OpenSans-Bold.woff2"), url("../fonts/OpenSans-Bold.woff"); }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.display-none {
  display: none; }

.page {
  height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 30px; }

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-height: 100%;
  margin: 0;
  background-color: #ffffff;
  color: #000000; }

.header {
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  color: red; }

.main {
  background-color: red; }

ol {
  list-style-type: none;
  /* Убираем исходные маркеры */
  counter-reset: item;
  /* Обнуляем счетчик списка */ }

li:nth-child(-n+9)::before {
  content: "0" counter(item);
  counter-increment: item;
  /* Задаём имя счетчика */ }

li:nth-child(n+10)::before {
  content: counter(item);
  counter-increment: item;
  /* Задаём имя счетчика */ }

.footer {
  color: red; }
